body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  color: black;
  background-color: rgb(21, 21, 21);
}

header {
  background-color: rgba(45, 39, 35, 0.313);
  color: white;
}

a {
  text-decoration: none;
  padding: 15px;
  color: rgb(226, 43, 180);
  transition: color 0.3s ease;
  font-family: Arial, Helvetica, sans-serif;
}

a:hover {
  color: white;
}

section {
  padding: 1rem;
  color: white;
}

#about {
  margin-left: 25px;
  margin-top: 50px;
  display: flex;
}

.apresentacao {
  padding: 45px;
  max-width: 600px;
}

.apresentacao1 {
  font-size: 46px;
  color: white;
  max-width: 600px;
}

.apresentacao2 {
  font-size: 46px;
  color: rgb(226, 43, 180);
  max-width: 600px;
}

.apresentacao3 {
  font-family: Arial;
}

.sobreMim {
  font-size: 30px;
  padding-top: 50px;
  color: rgb(226, 43, 180);
  font-family: Arial, Helvetica, sans-serif;
}

.descricao {
  color: white;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

img {
  box-shadow: 0 0 3px 3px rgb(226, 43, 180);
}

.flutuante {
  margin-left: 1350px;
}

#projects {
  color: rgb(226, 43, 180);
  margin-left: 55px;
  padding-top: 5px;
  font-size: 20px;
}

footer {
  color: white;
  margin-left: 830px;
}

.contatos {
  margin-left: 65px;
}

.contato2 {
  margin-left: 5px;
}

.conecte {
  color: rgb(226, 43, 180);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
}

.carousel {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px; /* Espa√ßamento entre os itens */
  padding: 20px;
}

.carousel-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%; /* Largura para garantir 3 itens por linha */
  margin-bottom: 20px;
}

.carousel-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.carousel-item img:hover {
  transform: scale(1.05); /* Zoom leve ao passar o mouse */
}

.carousel-item a {
  margin-top: 10px;
  font-size: 16px;
  color: #ff00ff;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.carousel-item a:hover {
  color: #fff; /* Altera a cor ao passar o mouse */
}

.button-project {
  padding: 8px;
  margin: 15px;
  border-radius: 8px;
  background-color: rgba(45, 39, 35, 0.313);
  border-color: black;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Adicione media queries para diferentes tamanhos de tela */
@media (max-width: 1200px) {
  #about {
    margin-left: 10px;
    margin-top: 30px;
    flex-direction: column;
    align-items: center;
  }

  .apresentacao,
  .apresentacao1,
  .apresentacao2 {
    font-size: 36px;
    max-width: 90%;
    padding: 20px;
  }

  .flutuante {
    margin-left: auto;
    margin-right: auto;
  }

  #projects {
    margin-left: 10px;
    font-size: 18px;
  }

  footer {
    margin-left: 0;
    text-align: center;
  }

  .contatos {
    margin-left: 10px;
  }
}

@media (max-width: 768px) {
  a {
    padding: 10px;
    font-size: 14px;
  }

  section {
    padding: 0.5rem;
  }

  .apresentacao {
    font-size: 24px;
  }

  .sobreMim {
    font-size: 24px;
    padding-top: 20px;
  }

  .descricao {
    font-size: 16px;
  }

  img {
    max-width: 90%;
    margin: 10px auto;
  }

  .carousel-item {
    width: 90%;
  }

  .button-project {
    margin: 10px;
    padding: 5px;
  }

  footer {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  #about {
    margin-left: 0;
    margin-top: 20px;
    flex-direction: column;
    align-items: center;
  }

  .apresentacao1,
  .apresentacao2 {
    font-size: 18px;
  }

  .apresentacao3 {
    font-size: 16px;
  }

  .carousel {
    flex-direction: column;
    gap: 10px;
  }

  .carousel-item {
    width: 100%;
  }

  .sobreMim,
  .conecte {
    font-size: 20px;
  }

  footer {
    margin-left: 0;
    text-align: center;
  }
}
